$popover-width: 80%;

.container {
  position: relative;
  svg {
    width: 10em;
  }
  .popover {
    position: absolute;
    background: white;
    border: 1px solid grey;
    border-radius: 0.5em;
    top: 10%;
    width: $popover-width;
    left: 50%;
    margin-left: $popover-width / -2;
  }
}

.mumsnet {
  circle {
    fill: hsl(204, 100%, 32%);
  }
}
